/* Base Styles */
.works {
  padding: 0 3rem;
  display: flex;
  height: 90vh;
  margin-top: 4rem;
  align-items: center;
}

.awesome {
  display: flex;
  flex-direction: column;
  position: relative;
}

.awesome > :nth-child(1) {
  color: var(--black);
  font-size: 2.5rem;
  font-weight: bold;
}

.awesome > :nth-child(2) {
  color: var(--orange);
  font-size: 2.5rem;
  font-weight: bold;
}

.awesome > :nth-child(3) {
  color: var(--gray);
  font-size: 14px;
  margin-top: 1rem;
}

.s-button {
  width: 8rem;
  height: 2rem;
  margin-top: 4rem;
}

/* Right side */
.w-right {
  position: relative;
}

.w-mainCircle {
  left: 9rem;
  position: relative;
  width: 18rem;
  height: 18rem;
  border-radius: 100%;
  box-shadow: var(--smboxShadow);
  top: 2rem;
  background: white;
  z-index: 2;
}

.w-mainCircle > * {
  position: absolute;
}

.w-mainCircle > :nth-child(1) {
  top: -3rem;
  left: 6rem;
}

.w-mainCircle > :nth-child(2) {
  left: -3rem;
  top: 5rem;
}

.w-mainCircle > :nth-child(3) {
  left: 6rem;
  top: 5rem;
}

.w-mainCircle > :nth-child(4) {
  left: 15rem;
  top: 5rem;
}

.w-mainCircle > :nth-child(5) {
  left: 6rem;
  top: 13rem;
}

.w-secCircle {
  width: 6rem;
  height: 6rem;
  position: absolute;
  border-radius: 100%;
  border: 5px solid #ebebeb;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  box-shadow: var(--smboxShadow);
  background: white;
}

.w-secCircle > img {
  transform: scale(0.6);
}

.w-backCircle {
  position: absolute;
  height: 13rem;
  width: 13rem;
  border-radius: 100%;
  z-index: 1;
}

.blueCircle {
  background: #1949b8;
  left: 18rem;
  top: 0rem;
}

.yellowCircle {
  background: #f5c32c;
  left: 18rem;
  top: 8rem;
}

/* Tablet and Mobile View (max-width: 768px) */
@media screen and (max-width: 768px) {
  .works {
    flex-direction: column;
    height: auto;
    gap: 4rem;
  }

  .w-right {
    transform: scale(0.8);
    left: 0;
    justify-content: center;
    display: flex;
  }

  .w-mainCircle {
    width: 15rem;
    height: 15rem;
    left: 0;
    top: 0;
    margin: 0 auto;
  }

  .w-mainCircle > * {
    top: 3rem;
    left: 3rem;
  }

  .w-backCircle {
    width: 10rem;
    height: 10rem;
  }

  .blueCircle {
    left: 12rem;
    top: 0;
  }

  .yellowCircle {
    left: 12rem;
    top: 6rem;
  }
}

/* Mobile View (max-width: 480px) */
@media screen and (max-width: 480px) {
  .works {
    flex-direction: column;
    height: auto;
    padding: 0;
    gap: 3rem;
  }

  .w-right {
    transform: scale(0.7);
    left: 0;
    justify-content: center;
  }

  .w-mainCircle {
    width: 12rem;
    height: 12rem;
    left: 0;
    top: 0;
    margin: 0 auto;
  }

  .w-mainCircle > * {
    left: 2rem;
    top: 2rem;
  }

  .w-backCircle {
    width: 8rem;
    height: 8rem;
  }

  .blueCircle {
    left: 10rem;
    top: 0;
  }

  .yellowCircle {
    left: 10rem;
    top: 5rem;
  }

  .s-button {
    width: 7rem;
    height: 2rem;
    font-size: 0.9rem;
  }
}
